.rating {
  display: flex;
  align-items: center;
  height: 32px;

  &__star {
    width: 32px;
    height: 32px;
    mask-image: url(../images/cards/star.png);
    mask-repeat: no-repeat;
    mask-size: 32px 32px;
    cursor: pointer;
    background-image: url("../images/cards/star.svg");
    background-repeat: no-repeat;
    background-size: contain;
  }

  &__value {
    display: inline-block;
    margin-left: 8px;
    font-family: "Roboto", sans-serif;
    font-size: 14px;
    font-weight: 500;
    line-height: 32px;
    color: $gray;
  }
}

@media screen and (max-width: 630px) {
  .rating {
    display: none;
  }
}
