.modals {
  position: fixed;
  z-index: 20;

  &__login {
    position: fixed;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    padding: 0 3vw;
    background-color: $gray;
  }

  &__login_open {
    animation: loginOpen 0.3s forwards;
  }

  &__login_close {
    animation: loginClose 0.3s forwards;
  }

  &__login-form {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: max-content;
    min-width: 20vw;
    height: max-content;
    min-height: 20vh;
    padding: 30px;
    padding-top: 50px;
    background-color: $bgWhite;
    border-radius: 10px;
  }

  &__login-input {
    padding: 10px;
    margin-bottom: 15px;
    font-size: 18px;
    border: 1px solid $black;
    border-radius: 5px;
  }

  &__login-text {
    font-size: 24px;
    font-weight: 600;
    line-height: 42px;
  }

  &__login-btn {
    display: inline-block;
    padding: 10px;
    margin: 10px 0;
    font-size: 18px;
  }

  &__login-btn:hover {
    color: $blue;
    background-color: $bgGray;
    box-shadow: 4px 4px 8px 0 rgba(50, 135, 201, 0.2);
  }

  &__login-btn:last-child {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 32px;
    height: 32px;
    padding: 0;
    font-size: 14px;
    line-height: 32px;
    border: 1px solid rgb(206, 56, 56);
    border-radius: 5px;
  }

  &__login-btn:last-child:hover {
    color: rgb(219, 20, 20);
    border: 2px solid rgb(219, 20, 20);
  }

  &__cart {
    position: fixed;
    top: 80px;
    right: 0;
    display: flex;
    width: 100%;
    max-width: 400px;
    height: max-content;
    max-height: calc(100vh - 80px);
    background-color: $bgWhite;
    border: 1px solid $blue;
    border-radius: 5px;
  }

  &__wrap-cart {
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow-y: auto;
  }

  &__item-product {
    display: flex;
    align-items: center;
    height: 130px;
  }

  &__item-product:nth-child(odd) {
    background-color: $bgGray;
  }

  &__illust-product {
    width: 130px;
    height: 130px;
    padding: 10px;
    border-radius: 20px;
  }

  &__count-item-wrap {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    width: 60px;
  }

  &__title-product {
    display: flex;
    height: 100px;
    padding: 10px;
    overflow: hidden;
    font-size: 16px;
    line-height: 1.4em;
  }

  &__btn-minus {
    display: block;
    width: 32px;
    height: 32px;
    font-weight: 600;
    line-height: 18px;
    color: red;
    background-color: transparent;
    border: 1px solid red;
    border-radius: 50%;
  }

  &__btn-plus {
    display: block;
    width: 32px;
    height: 32px;
    font-weight: 600;
    line-height: 18px;
    color: green;
    background-color: transparent;
    border: 1px solid green;
    border-radius: 50%;
  }

  &__count-item {
    display: inline-block;
    width: 3em;
    padding: 10px 0;
    font-size: 18px;
    font-weight: 600;
    text-align: center;
  }

  &__login-menu {
    position: fixed;
    top: 80px;
    right: 0;
    width: 100%;
    max-width: 400px;
    height: max-content;
    max-height: calc(100vh - 80px);
    overflow-y: auto;
    background-color: $bgWhite;
    border: 1px solid $blue;
    border-radius: 5px;
  }

  &__login-menu-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;
  }

  &__login-menu-item {
    position: relative;
    display: flex;
    padding: 10px 25px;
    margin: 10px 0;
    overflow: hidden;
    font-size: 20px;
    line-height: 24px;
    cursor: pointer;
  }

  &__login-menu-item::before {
    position: absolute;
    bottom: 0;
    left: -100%;
    z-index: -2;
    display: inline-block;
    width: 100%;
    height: 2px;
    content: "";
    background-color: $blue;
    animation: centerToRight 0.8s;
  }

  &__login-menu-item::after {
    position: absolute;
    bottom: 2px;
    left: 0;
    z-index: -2;
    display: inline-block;
    width: 100%;
    height: 0;
    content: "";
    background-color: $bgGray;
    border-radius: 5px;
    animation: fullToBottom 0.4s;
  }

  &__login-menu-item:hover:before {
    animation: leftToCenter 0.4s forwards;
  }

  &__login-menu-item:hover:after {
    animation: bottomToFull 0.4s forwards;
    animation-delay: "bottomToFull" 0.4s;
  }
}
