.account {
  display: flex;
  align-items: center;
  justify-content: flex-end;

  &__btn {
    padding: 8px 12px;
    margin-right: 21px;
    font-family: 'Quicksand', sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: bold;
    line-height: 150%;
    color: $blue;
    cursor: pointer;
    background: #fff;
    border: 1px solid $blue;
    border-radius: 4px;
    transition: box-shadow 0.2s;
  }

  &__btn:hover {
    box-shadow: 0 2px 4px rgba(27, 78, 163, 0.2), 0 4px 8px rgba(41, 121, 255, 0.2);
    transition: box-shadow 0.2s;
  }

  &__menu {
    display: block;
    width: 40px;
    height: 40px;
    margin-right: 16px;
    cursor: pointer;
    background: url('../images/userPic.jpg') no-repeat center;
    border-radius: 50%;
  }
}

@media screen and (max-width: 790px) {
  .account {
    &__btn {
      margin-right: 15px;
      font-size: 16px;
    }

    &__menu {
      margin: 0;
    }
  }
}
