@import 'reset';
@import 'fonts';
@import 'visually-hidden';
@import '_variables.scss';
@import '_animations.scss';
@import '_appbar.scss';
@import '_account.scss';
@import '_top-nav.scss';
@import '_board-sales.scss';
@import '_departments.scss';
@import '_products.scss';
@import '_rating.scss';
@import '_product.scss';
@import '_modals.scss';
@import '_footer.scss';

body {
  padding-top: 80px;
  font-family: 'Inter', sans-serif;
  background-color: $bgWhite;
}

.hidde {
  display: none;
}

.page {
  display: flex;
  flex-shrink: 0;
  justify-content: space-between;
  width: 100%;
  padding: 0 3%;
}

use {
  display: block;
  width: 100%;
  height: 100%;
}

.stopScroll {
  overflow: hidden;
}

@media screen and (max-width: 1370px) {
  .swiper-button-prev {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 100%;
    background-image: url('../images/arrowRight.svg');
    background-repeat: no-repeat;
    background-position: center;
  }

  .swiper-button-next {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 100%;
    background-image: url('../images/arrowLeft.svg');
    background-repeat: no-repeat;
    background-position: center;
  }
}

.swiper-button-prev::after {
  display: none;
}

.swiper-button-next::after {
  display: none;
}

.not-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 70%;
  color: $black;
  text-align: center;

  &__title {
    font-size: 15vw;
    font-weight: 600;
  }

  &__text {
    padding: 20px;
    font-size: 3vw;
    line-height: 1.5em;
  }
}

#cart {
  position: relative;
}

#count-product {
  position: absolute;
  top: -5px;
  right: -5px;
  display: inline-block;
  width: 20px;
  height: 20px;
  padding: 0;
  font-size: 14px;
  line-height: 20px;
  color: white;
  text-align: center;
  background-color: red;
  border-radius: 50%;
}

@media screen and (max-width: 580px) {
  body {
    padding-top: 160px;
  }
}
