@keyframes upToCenter {
  0% {
    display: none;
  }

  1% {
    display: block;
    transform: translate(0, 0);
  }

  100% {
    transform: translate(100%, 100%);
  }
}

@keyframes leftToCenter {
  100% {
    left: 0;
  }
}

@keyframes centerToRight {
  0% {
    left: 0;
  }

  50% {
    left: 0;
  }

  100% {
    left: 100%;
  }
}

@keyframes fullToBottom {
  0% {
    height: 100%;
  }

  100% {
    height: 0;
  }
}

@keyframes bottomToFull {
  0% {
    height: 0;
  }

  100% {
    height: 100%;
  }
}

@keyframes loginOpen {
  0% {
    right: -100%;
  }

  100% {
    right: 0;
  }
}

@keyframes loginClose {
  0% {
    right: 0;
  }

  100% {
    right: -100%;
  }
}

@keyframes appbarOpen {
  0% {
    top: -100%;
  }

  100% {
    top: 0;
  }
}

@keyframes appbarClose {
  0% {
    top: 0;
  }

  100% {
    top: -100%;
  }
}
