.board-sales {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 24px;

  &__wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 0 2vw;
    padding-bottom: 80px;
    border-radius: 20px;
    box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
  }

  &__filters {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    grid-row: 1/2;
    grid-column: 1/5;
    justify-content: flex-end;
    padding: 40px 16px;
  }

  &__filters_list {
    width: 100%;
  }

  &__selects {
    position: absolute;
    top: 40px;
    left: 16px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 16px;
  }

  &__select {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    flex-grow: 0;
    width: 100%;
    min-width: 160px;
    height: max-content;
    background: url('../images/arrowDown.png');
    background-color: white;
    background-repeat: no-repeat;
    background-position: top 6px right 6px;
    border: 1px solid $gray;
    border-radius: 5px;
  }

  &__select:not(:last-child) {
    margin-right: 16px;
  }

  &__filter-item {
    padding: 6px 12px;
    padding-right: 36px;
    font-size: 14px;
    font-weight: 500;
    line-height: 170%;
    cursor: pointer;
  }

  &__filter-item:hover {
    z-index: 0;
    color: $blue;
    background: linear-gradient(90deg, rgba(235, 242, 255, 1) 0, rgba(235, 242, 255, 1) 60%, rgba(125, 186, 232, 0) 80%);
    border-radius: 5px;
  }

  &__show-filter {
    display: flex;
    margin-right: 24px;
    background-color: $bgGray;
    border-radius: 5px;
  }

  &__btn-showFl {
    padding: 11px 15px;
    font-size: 14px;
    color: $gray;
    letter-spacing: 0.018em;
    background-color: transparent;

    &_activ {
      font-weight: 600;
      color: $blue;
      background-color: #fff;
      border-radius: 5px;
      box-shadow: 0 3px 5px 0 rgba(90, 91, 106, 0.9);
    }
  }

  &__preview-type {
    display: flex;
    background-color: $bgGray;
    border-radius: 5px;
  }

  &__btn-changePw {
    position: relative;
    padding: 4px 20px;
    color: $gray;
    letter-spacing: 0.018em;
    background-color: transparent;

    &_activ {
      color: $blue;
      background-color: #fff;
      border-radius: 5px;
      box-shadow: 0 3px 5px 0 rgba(90, 91, 106, 0.9);
    }
  }

  &__btn-changePw::before {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    display: inline-block;
    width: 100%;
    height: 100%;
    content: "";
  }

  &__svg {
    z-index: -1;
    width: 28px;
    height: 28px;
    fill: $gray;

    &_activ {
      fill: $blue;
    }
  }

  &__related {
    position: relative;
    display: flex;
    width: 100%;
    margin-top: 40px;
  }

  &__title {
    margin-right: 10px;
    font-size: 14px;
    font-weight: 500;
    line-height: 143%;
  }

  &__related-items {
    display: flex;
    flex-shrink: 1;
    flex-wrap: wrap;
  }

  &__related-item {
    display: inline-block;
    width: max-content;
    padding: 4px 10px;
    background-color: $bgGray;
    border-radius: 7px;
  }

  &__related-item:not(:last-child) {
    margin-right: 10px;
  }

  &__related-link {
    display: inline-block;
    width: 100%;
    font-size: 14px;
    color: $gray;
  }

  &__btn-related-more {
    position: absolute;
    display: none;
    width: 32px;
    height: 32px;
    background-color: transparent;
  }

  &__btn-related-svg {
    width: 100%;
    height: 100%;
  }

  &__products {
    display: grid;
    grid-template-rows: repeat(5, auto);
    grid-template-columns: repeat(4, auto);
    gap: 25px;
  }

  &__products_list {
    display: flex;
    flex-direction: column;
  }
}

@media screen and (max-width: 1520px) {
  .board-sales {
    &__filters {
      justify-content: space-between;
    }

    &__selects {
      top: 135px;
    }

    &__related {
      margin-top: 140px;
    }

    &__related-item {
      margin-bottom: 12px;
    }

    &__products {
      grid-template-rows: repeat(5, auto);
      grid-template-columns: repeat(3, auto);
    }

    &__products_list {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1300px) {
  .board-sales {
    width: 100%;
    margin: 0 auto;

    &__products {
      grid-template-rows: repeat(5, auto);
      grid-template-columns: repeat(4, auto);
    }

    &__filters {
      padding: 16px;
    }
  }
}

@media screen and (max-width: 1090px) {
  .board-sales {
    &__products {
      grid-template-rows: repeat(5, auto);
      grid-template-columns: repeat(3, auto);
    }
  }
}

@media screen and (max-width: 810px) {
  .board-sales {
    &__products {
      grid-template-rows: repeat(5, auto);
      grid-template-columns: repeat(2, auto);
    }
  }
}

@media screen and (max-width: 630px) {
  .board-sales {
    &__preview-type {
      display: none;
    }

    &__show-filter {
      margin-right: 0;
    }

    &__selects {
      width: 100%;
    }

    &__select {
      position: absolute;
      width: max-content;
      min-width: 160px;
    }

    &__select:nth-child(1) {
      z-index: 5;
    }

    &__select:nth-child(2) {
      right: 3%;
      left: auto;
    }

    &__select:nth-child(3) {
      top: 60px;
      right: auto;
      left: 0;
    }

    &__related {
      height: 34px;
      padding-right: 32px;
      margin-top: 195px;
      overflow: hidden;
    }

    &__title {
      font-size: 22px;
      line-height: 32px;
    }

    &__related-items {
      padding-top: 3px;
    }

    &__related-link {
      font-size: 18px;
    }

    &__btn-related-more {
      top: 0;
      right: 0;
      display: block;
      padding: 5px;
      border: 1px solid $gray;
      border-radius: 50%;
    }

    &__btn-related-svg {
      fill: $gray;
    }
  }
}

@media screen and (max-width: 390px) {
  .board-sales {
    &__products {
      gap: 10px;
    }

    &__select:nth-child(2) {
      top: 60px;
      left: 0;
      z-index: 4;
    }

    &__select:nth-child(3) {
      top: 120px;
      right: auto;
      left: 0;
      z-index: 3;
    }

    &__related {
      margin-top: 245px;
    }

    &__btn-showFl {
      padding: 12px 12px;
    }
  }
}
