.footer {
  width: 100%;

  &__wrapper {
    display: grid;
    grid-template-rows: 40px repeat(2, auto);
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    width: 100%;
    padding: 45px 3vw;
  }

  &__title {
    font-size: 32px;
    font-weight: 600;
    color: $black;
  }

  &__about-us {
    grid-row: 1/2;
    grid-column: 1/2;
  }

  &__items-about {
    display: flex;
    flex-direction: column;
    grid-row: 2/3;
    grid-column: 1/2;
    align-items: flex-start;
  }

  &__item-about:not(:last-child) {
    margin-bottom: 10px;
  }

  &__link {
    position: relative;
    display: inline-block;
    width: max-content;
    padding: 5px;
    overflow: hidden;
    font-size: 18px;
    line-height: 22px;
    color: $black;
  }

  &__link::before {
    position: absolute;
    bottom: 0;
    left: -100%;
    z-index: -2;
    display: inline-block;
    width: 100%;
    height: 2px;
    content: "";
    background-color: $blue;
    animation: centerToRight 0.8s;
  }

  &__link::after {
    position: absolute;
    bottom: 2px;
    left: 0;
    z-index: -2;
    display: inline-block;
    width: 100%;
    height: 0;
    content: "";
    background-color: $bgGray;
    border-radius: 5px;
    animation: fullToBottom 0.4s;
  }

  &__link:hover:before {
    animation: leftToCenter 0.4s forwards;
  }

  &__link:hover:after {
    animation: bottomToFull 0.4s forwards;
    animation-delay: "bottomToFull" 0.4s;
  }

  &__contacts {
    display: grid;
    grid-template-rows: repeat(6, auto);
    grid-template-columns: 1fr;
    grid-row: 1/3;
    grid-column: 2/3;
    gap: 16px;
    align-items: flex-start;
    width: 100%;
  }

  &__text {
    font-size: 16px;
    line-height: 20px;
    color: $gray;
  }

  &__subtitle {
    font-size: 22px;
    font-weight: 500;
    line-height: 26px;
    color: $black;
  }

  &__socialLinks {
    display: flex;
    padding: 20px 0;
  }

  &__item-social {
    display: inline-block;
    width: 48px;
    height: 48px;
  }

  &__item-social:not(:last-child) {
    margin-right: 15px;
  }

  &__link-social {
    display: inline-block;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    transition: transform 0.3s;
  }

  &__link-social:hover {
    border-radius: 10px;
    box-shadow: 4px 4px 8px 0 rgba(40, 136, 210, 0.42);
    transition: transform 0.3s;
    transform: scaleY(1.1) translateY(-3px);
  }

  &__item-social:first-child &__link-social {
    background-image: url("../images/android.svg");
  }

  &__item-social:last-child &__link-social {
    background-image: url("../images/twitter.svg");
  }

  &__item-social:nth-child(2) &__link-social {
    background-image: url("../images/apple.svg");
  }

  &__item-social:nth-child(3) &__link-social {
    background-image: url("../images/youtube.svg");
  }

  &__small {
    grid-row: 3/4;
    grid-column: 1/3;
    text-align: center;
  }
}

@media screen and (max-width: 480px) {
  .footer {
    &__wrapper {
      display: flex;
      flex-direction: column;
    }

    &__items-about {
      margin-bottom: 35px;
    }
  }
}
