.appbar {
  position: absolute;
  top: 0;
  left: 0;
  display: grid;
  grid-template-rows: 80px;
  grid-template-columns: repeat(9, auto);
  gap: 21px;
  align-items: center;
  width: 100%;

  &__logo {
    display: inline-block;
    grid-column: 1/2;
    width: 208px;
    height: 80px;
    background-image: url('../images/Logo.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &__link {
    position: relative;
    display: flex;
    justify-content: center;
    overflow: hidden;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 158%;
    color: $blue;
  }

  &__link::before {
    position: absolute;
    bottom: 0;
    left: -100%;
    z-index: -2;
    display: inline-block;
    width: 100%;
    height: 2px;
    content: "";
    background-color: $blue;
    animation: centerToRight 0.8s;
  }

  &__link::after {
    position: absolute;
    bottom: 2px;
    left: 0;
    z-index: -2;
    display: inline-block;
    width: 100%;
    height: 0;
    content: "";
    background-color: $bgGray;
    border-radius: 5px;
    animation: fullToBottom 0.4s;
  }

  &__link:hover:before {
    animation: leftToCenter 0.4s forwards;
  }

  &__link:hover:after {
    animation: bottomToFull 0.4s forwards;
    animation-delay: "bottomToFull" 0.4s;
  }

  &__search {
    position: relative;
    display: flex;
    grid-column: 4/6;
    width: 100%;
    max-width: 668px;
    height: 48px;
  }

  &__btn-search {
    position: absolute;
    top: 0;
    right: 40px;
    z-index: 5;
    display: none;
    width: 48px;
    height: 48px;
    background-color: transparent;
    background-image: url('../images/search.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 32px 32px;
  }

  &__btn-search_activ {
    display: block;
  }

  &__input {
    display: flex;
    width: 100%;
    height: 100%;
    padding: 8px;
    font-size: 20px;
    font-style: normal;
    font-weight: normal;
    line-height: 158%;
    color: #19191d;
    background-color: $bgGray;
    border-radius: 99px;
  }

  &__account {
    grid-column: 7/10;
  }

  &_fixed {
    position: fixed;
    top: 0;
    z-index: 40;
    background-color: $bgWhite;
  }
}

@media screen and (max-width: 1370px) {
  .appbar {
    grid-template-rows: 80px;
    grid-template-columns: 210px 1fr 1fr;

    &__search {
      grid-row: 1/2;
      grid-column: 2/3;
      width: 94%;
      margin: 0 auto;
    }

    &__account {
      grid-row: 1/2;
      grid-column: 3/4;
    }

    &__link {
      display: none;
    }
  }
}

@media screen and (max-width: 880px) {
  .appbar {
    grid-template-rows: 80px;
    grid-template-columns: 40px 1fr 1fr;
    gap: 0;
    padding: 0 3%;

    &__search {
      grid-row: 1/2;
      grid-column: 2/3;
      justify-self: center;
    }

    &__account {
      grid-row: 1/2;
      grid-column: 2/4;
    }

    &__logo {
      width: 40px;
      height: 40px;
      background: url('../images/logoTiny.png');
    }
  }
}

@media screen and (max-width: 580px) {
  .appbar {
    grid-template-rows: 80px 80px;
    grid-template-columns: 40px 1fr;

    &__search {
      grid-row: 2/3;
      grid-column: 1/3;
    }
  }
}

.appBarOpen {
  animation: appbarOpen 0.3s forwards;
}

.appBarClose {
  animation: appbarClose 0.3s forwards;
}
