.departments {
  position: relative;
  flex-shrink: 0;
  width: 355px;
  padding: 25px;
  margin-right: 25px;

  &__btn {
    position: sticky;
    top: 20px;
    z-index: 10;
    display: flex;
    padding: 8px 20px;
    margin: 10px;
    font-family: "Quicksand", sans-serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 36px;
    color: $blue;
    background-color: $bgWhite;
    border: 1px solid $blue;
    border-radius: 5px;
  }

  &__btn:hover {
    box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
  }

  &__btn-svg {
    width: 32px;
    height: 32px;
    margin-right: 8px;
    fill: $blue;
  }

  &__list {
    position: sticky;
    top: 90px;
    overflow-y: auto;
  }

  &__all-category {
    position: relative;
    padding: 8px 0 8px 24px;
    font-size: 16px;
    font-weight: 600;
    line-height: 24px;
    color: $black;
    letter-spacing: 0.0275em;
    cursor: pointer;
  }

  &__sub-category {
    position: relative;
    padding: 8px 0 8px 24px;
    margin-left: 25px;
    font-weight: 500;
    color: $black;
  }

  &__sub-category::before {
    position: absolute;
    left: 0;
    z-index: 10;
    display: block;
    width: 24px;
    height: 24px;
    content: "";
  }

  &__sub-category:hover {
    color: $blue;
  }

  &__item-category {
    padding: 8px 0;
    margin-left: 25px;
    font-weight: 400;
    color: $black;
  }

  &__item-category:hover {
    color: $blue;
  }

  &__list-chevron {
    position: absolute;
    top: 2px;
    left: 0;
    width: 24px;
    height: 24px;
    margin-top: 5px;
    transition: transform 0.3s;
    transform: rotate(-90deg);
    transform-origin: center center;

    &_open {
      transition: transform 0.3s;
      transform: rotate(0);
    }
  }

  &__sub-category:hover &__list-chevron {
    fill: $blue;
  }
}

@media screen and (max-width: 1300px) {
  .departments {
    position: relative;
    top: 0;
    left: 0;
    z-index: 10;
    width: max-content;
    height: max-content;
    padding: 0;
    margin: 0;

    &__btn {
      position: absolute;
      top: 100px;
      left: calc(2vw + 16px);
      margin: 0;
      font-size: 14px;
      line-height: 22px;
      letter-spacing: 0.018em;
      transition: 0.3s;
    }

    &__btn_activ {
      position: fixed;
      top: 10px;
      left: 3%;
      z-index: 10;
      overflow: hidden;
      background-color: $bgGray;
      transition: 0.3s;
      transform: rotate(0);
    }

    &__btn-svg {
      width: 22px;
      height: 22px;
    }

    &__list {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 9;
      display: block;
      height: 100%;
      padding: 80px 50px 0 50px;
      background-color: $bgGray;
      transition: transform 0.3s;
      transform: translate(-100%, 100%);
    }

    &__list_activ {
      transition: transform 0.3s;
      transform: translate(0, 0);
    }
  }
}

@media screen and (max-width: 1180px) {
  .departments {
    &__btn {
      margin-left: 0;
    }
  }
}
