.products {
  &__card {
    display: grid;
    grid-template-rows: 224px repeat(5, max-content);
    grid-template-columns: repeat(2, 1fr);
    row-gap: 10px;
    column-gap: 16px;
    align-items: center;
    justify-items: center;
    height: max-content;
    min-height: 472px;
    padding: 16px;
  }

  &__card:hover {
    border-radius: 10px;
    box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
  }

  &__card_list {
    grid-template-rows: 74px repeat(3, auto);
    grid-template-columns: repeat(4, auto);
    row-gap: 10px;
    column-gap: 16px;
    width: 100%;
    min-height: initial;
    border: 2px solid $gray;
    border-radius: 5px;
  }

  &__card_list:not(:last-child) {
    margin-bottom: 9px;
  }

  &__illust {
    display: inline-block;
    grid-row: 1/2;
    grid-column: 1/3;
    width: 100%;
    max-width: 226px;
    height: auto;
    max-height: 224px;
  }

  &__illust_list {
    grid-row: 1/3;
    grid-column: 1/2;
  }

  &__link {
    display: inline-block;
    grid-row: 2/3;
    grid-column: 1/3;
    height: 48px;
    overflow: hidden;
    font-size: 16px;
    line-height: 24px;
    color: $black;
    text-align: center;
    letter-spacing: 0.0275em;
  }

  &__link:hover {
    color: $blue;
    text-decoration: underline;
  }

  &__link_list {
    grid-row: 1/2;
    grid-column: 2/5;
    height: max-content;
  }

  &__cost {
    grid-row: 3/4;
    grid-column: 1/3;
    font-size: 24px;
    font-weight: bold;
    line-height: 36px;
  }

  &__cost::after {
    content: "$";
  }

  &__cost_list {
    grid-row: 3/4;
    grid-column: 2/3;
    justify-self: end;
  }

  &__additional {
    grid-row: 4/5;
    grid-column: 1/3;
    height: 40px;
    overflow: hidden;
    font-size: 14px;
    line-height: 20px;
    color: $gray;
    letter-spacing: 0.018em;
  }

  &__additional_list {
    grid-row: 2/3;
    grid-column: 2/5;
  }

  &__rating {
    grid-row: 5/6;
    grid-column: 1/3;
  }

  &__rating_list {
    grid-row: 3/4;
    grid-column: 1/2;
    justify-self: center;
  }

  &__btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 32px;
    border: 1px solid $gray;
    border-radius: 5px;
  }

  &__btn:hover {
    font-weight: bold;
    color: $blue;
    border: 1px solid $blue;
    border-radius: 5px;
    box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
  }

  &__watch {
    grid-row: 6/7;
    grid-column: 1/2;
    width: 88px;
    padding-left: 12px;
  }

  &__to-cart {
    grid-row: 6/7;
    grid-column: 2/3;
    width: 100px;
    padding-left: 24px;
  }

  &__btn-svg {
    position: absolute;
    top: 0;
    left: 2px;
    width: 28px;
    height: 28px;
    fill: $gray;
  }

  &__watch:hover &__btn-svg {
    fill: $blue;
  }

  &__to-cart:hover &__btn-svg {
    fill: $blue;
  }

  &__watch_list {
    grid-row: 3/4;
    grid-column: 3/4;
    justify-self: end;
  }

  &__to-cart_list {
    grid-row: 3/4;
    grid-column: 4/5;
    justify-self: start;
  }

  &__btn-show-more {
    position: absolute;
    bottom: 25px;
    display: block;
    width: calc(100% - 4vw);
    height: 38px;
    font-size: 20px;
    line-height: 38px;
    color: $black;
    background-color: transparent;
    border: 1px solid $blue;
    border-radius: 10px;
  }

  &__btn-show-more:hover {
    background-color: $bgGray;
    box-shadow: 4px 4px 8px 0 rgba(34, 60, 80, 0.2);
  }
}

@media screen and (max-width: 630px) {
  .products {
    &__card {
      display: flex;
      flex-direction: column;
      align-items: center;
      min-width: 140px;
      min-height: initial;
      border: 1px solid $gray;
      border-radius: 10px;
    }

    &__additional {
      display: none;
    }

    &__illust {
      width: 100%;
    }
  }
}
