.top-nav {
  position: relative;
  display: flex;
  width: 100%;
  padding: 0 50px;
  overflow: hidden;

  &__list {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  &__item {
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    width: max-content;
    padding: 4px;
  }

  &__item:hover {
    background: #ebf2ff;
    border-radius: 10px;
  }

  &__link {
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 143%;
    color: $gray;
    text-align: center;
  }

  &__item:hover &__link {
    color: $blue;
  }

  &__item-illustration {
    display: block;
    width: 20px;
    height: 20px;
    margin: 4px auto;
    fill: $gray;
  }

  &__item:hover &__item-illustration {
    fill: $blue;
  }
}

@media screen and (max-width: 1366px) {
  .top-nav {
    &::before {
      position: absolute;
      left: 0;
      z-index: 8;
      width: 50px;
      height: 100%;
      content: "";
      background-color: $bgWhite;
    }

    &::after {
      position: absolute;
      right: 0;
      z-index: 8;
      width: 50px;
      height: 100%;
      content: "";
      background-color: $bgWhite;
    }

    &__item {
      width: calc(100% - 100px / 7);
    }
  }
}

@media screen and (max-width: 1000px) {
  .top-nav {
    &__item {
      width: calc(100% - 100px / 5);
    }
  }
}
