.product {
  display: grid;
  grid-template-rows: max-content 750px;
  grid-template-columns: 1fr 1fr;

  &__wrapper {
    display: grid;
    grid-template-rows: repeat(5, max-content);
    grid-template-columns: 6vw 20vw 10vw 10vw calc(48vw - 80px);
    grid-row: 1/2;
    grid-column: 1/3;
    gap: 20px;
    width: 100%;
    padding: 45px 3vw;
  }

  &__illustrations-items {
    display: flex;
    flex-direction: column;
    grid-row: 1/6;
    grid-column: 1/2;
    align-items: center;
    justify-content: space-between;
    width: 100px;
    height: 400px;
    border: 2px solid $gray;
    border-radius: 5px;
  }

  &__illustrations-item {
    display: flex;
    flex-shrink: 0;
    width: 96%;
    height: auto;
    object-fit: cover;
    transition: transform 0.2s;

    &_active {
      border-radius: 5px;
      box-shadow: 0 5px 10px 2px rgba(66, 137, 191, 0.2);
    }
  }

  &__illustrations-item:hover {
    box-shadow: 2px 2px 7px 0 rgba(34, 60, 80, 0.4);
    transition: transform 0.2s;
    transform: scale(1.1);
  }

  &__illustration {
    grid-row: 1/6;
    grid-column: 2/3;
    width: 100%;
    height: 100%;
    max-height: 400px;
  }

  &__title {
    grid-row: 1/2;
    grid-column: 3/6;
    font-size: 36px;
    font-weight: 600;
    line-height: 40px;
    color: $black;
  }

  &__cost {
    grid-row: 2/3;
    grid-column: 3/4;
    font-size: 32px;
    font-weight: 500;
    line-height: 36px;
  }

  &__cost::after {
    content: " $";
  }

  &__rating {
    display: flex;
    flex-wrap: wrap;
    grid-row: 2/3;
    grid-column: 4/6;
    height: initial;
  }

  &__orders-recall {
    width: 100%;
    color: $gray;
  }

  &__description {
    grid-row: 3/5;
    grid-column: 3/6;
    font-size: 24px;
    font-weight: 400;
    line-height: 30px;
  }

  &__btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 32px;
    border: 1px solid $gray;
    border-radius: 5px;
  }

  &__btn:hover {
    font-weight: bold;
    color: $blue;
    border: 1px solid $blue;
    border-radius: 5px;
    box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
  }

  &__watch {
    grid-row: 5/6;
    grid-column: 3/4;
    width: 88px;
    padding-left: 12px;
  }

  &__to-cart {
    grid-row: 5/6;
    grid-column: 4/5;
    width: 100px;
    padding-left: 24px;
  }

  &__btn-svg {
    position: absolute;
    top: 0;
    left: 2px;
    width: 28px;
    height: 28px;
    fill: $gray;
  }

  &__watch:hover &__btn-svg {
    fill: $blue;
  }

  &__to-cart:hover &__btn-svg {
    fill: $blue;
  }

  &__similar {
    display: flex;
    flex-direction: column;
    padding: 3vw;
    padding-right: 1.5vw;
  }

  &__similar-title {
    padding-bottom: 40px;
    font-size: 32px;
  }

  &__similar-wrap {
    display: grid;
    grid-template-rows: 100%;
    grid-template-columns: 1fr 1fr;
    grid-gap: 25px;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0 5px 10px 2px rgba(73, 157, 221, 0.2);
  }

  &__comments {
    display: flex;
    flex-direction: column;
    padding: 3vw;
    padding-left: 1.5vw;
  }

  &__comment-title {
    padding-bottom: 40px;
    font-size: 32px;
  }

  &__comments-items {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding: 16px;
    overflow-x: hidden;
    overflow-y: auto;
    border-radius: 20px;
    box-shadow: 0 5px 10px 2px rgba(73, 157, 221, 0.2);
  }

  &__comment-item {
    display: grid;
    grid-template-rows: 32px max-content auto;
    grid-template-columns: 100px min-content auto;
    gap: 15px;
    margin-bottom: 16px;
    background-repeat: 10px;
  }

  &__comment-item:nth-child(even) {
    background-color: $bgGray;
  }

  &__user-pic {
    grid-row: 1/4;
    grid-column: 1/2;
    width: 80px;
    height: 80px;
    padding: 10px;
  }

  &__author {
    grid-row: 1/2;
    grid-column: 2/3;
    font-size: 24px;
    line-height: 32px;
  }

  &__comment-text {
    grid-row: 2/-1;
    grid-column: 2/-1;
    padding-bottom: 15px;
    font-size: 14px;
    line-height: 20px;
  }

  &__star {
    display: inline-block;
    width: 32px;
    height: 32px;
    background-image: url('../images/cards/star.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &__rating-value {
    font-size: 14px;
    line-height: 47px;
  }
}

@media screen and (max-width: 1366px) {
  .product {
    &__wrapper {
      grid-template-columns: 10vw 40vw 12vw 12vw calc(20vw - 80px);
    }

    &__cost {
      font-size: 24px;
    }
  }
}

@media screen and (max-width: 980px) {
  .product {
    &__wrapper {
      grid-template-columns: 20vw 40vw calc(34vw - 40px);
    }

    &__title {
      grid-column: 1/4;
    }

    &__cost {
      grid-column: 1/2;
      font-size: 18px;
    }

    &__rating {
      grid-column: 2/3;
    }

    &__illustrations-items {
      grid-row: 3/4;
    }

    &__illustration {
      grid-row: 3/4;
    }

    &__description {
      grid-row: 4/5;
      grid-column: 1/4;
    }

    &__watch {
      grid-column: 1/2;
    }

    &__to-cart {
      grid-column: 2/3;
    }

    &__comments {
      grid-column: 1/3;
      padding-left: 3vw;
    }

    &__similar {
      grid-row: 3/4;
      grid-column: 1/3;
      padding-right: 3vw;
    }

    &__similar-wrap {
      grid-gap: 15px;
      padding: 15px;
    }
  }
}

@media screen and (max-width: 685px) {
  .product {
    &__illustration {
      grid-row: 3/4;
      grid-column: 2/4;
    }

    &__user-pic {
      width: 50px;
      height: 50px;
      padding: 5;
    }

    &__comment-text {
      padding: 16px 0;
    }
  }
}

@media screen and (max-width: 480px) {
  .product {
    &__wrapper {
      grid-template-columns: 30vw calc(64vw - 20px);
    }

    &__title {
      grid-column: 1/3;
    }

    &__cost {
      font-size: 18px;
    }

    &__rating {
      grid-column: 2/3;
    }

    &__illustration {
      grid-column: 2/3;
      height: auto;
    }

    &__description {
      grid-column: 1/3;
    }

    &__to-cart {
      grid-column: 2/3;
    }

    &__comments-items {
      max-height: 50vh;
    }
  }
}

@media screen and (max-width: 360px) {
  .product {
    &__similar {
      padding: 2vw;
    }

    &__similar-wrap {
      grid-gap: 10px;
      padding: 10px;
    }
  }
}
